import fetch from 'isomorphic-unfetch';
import Layout from '../components/MyLayout.js';
import Search from '../components/Search';

export default class extends React.Component {

  //fetch data from localStorage or from API
  componentDidMount() {
    if(localStorage.getItem('appState')!="null" && localStorage.getItem('appState')) {
      const state = JSON.parse(localStorage.getItem("appState"))
      const data = state.data;
      this.setState({data});
    } else {
      fetch("https://pokeapi.co/api/v2/pokemon?limit=718")
        .then(response => response.json())
        .then(response => this.setState({data: response}))
        .then(() => localStorage.setItem('appState', JSON.stringify(this.state)));
    }
  }

  render() {
    if(this.state) {
      return (
          <Layout>
            <Search />
            <div>pqijvprovnqeprubqepirubvqperpq  qgnpqiurbgpub qiuefbqpiwurf qwueghpqirugbqpirgb rqpgubqpirugb qrugpqrugbqpiru qrgpunqprug rpguqrpgiub gpquirbgp qrb giurbgpuiqr gpiubgq purgbq pgiub pgubq guqw pgur pgub pigubr ipugb rpiugb eqpirugbeqirpugb pierubgipurbg pirugbre q</div>
            <p>{this.state.data.count}</p>
            <img src="./src/images/icons/searchicon.png" />
          </Layout>
      )
    } else {
      return (
          <Layout>
             <p>loading...</p>
          </Layout>
      )
    }
  }
}
